import React, { useState } from 'react';

export const showLoading = ( loading , message = 'Loading data .....' ) => {

    return ( loading &&  <div className="container">
              <h2 className="text-primary  text-center loading "> {message} </h2>
            </div>);
    } 
  

export  const Loading = ( msg ) => {
      return (
          <div className="container">
               <h2 className="text-primary text-center loading"> {msg} </h2>
          </div> 
      );    
}



export const   formatDate = ( date ) => {
    console.log(date);
    // covert createdAt to timeStamp
    // let date = createdAt.seconds *1000; // in milliseconds
    let diff = new Date() - date; // the difference in milliseconds
  
    if (diff < 1000) { // less than 1 second
      return 'right now';
    }
  
    let sec = Math.floor(diff / 1000); // convert diff to seconds
  
    if (sec < 60) {
      return sec + ' sec. ago';
    }
  
    let min = Math.floor(diff / 60000); // convert diff to minutes
    if (min < 60) {
      return min + ' min. ago';
    }
  
    // format the date
    // add leading zeroes to single-digit day/month/hours/minutes
    let d = date;
    d = [
      '0' + d.getDate(),
      '0' + (d.getMonth() + 1),
      '' + d.getFullYear(),
      '0' + d.getHours(),
      '0' + d.getMinutes()
    ].map(component => component.slice(-2)); // take last 2 digits of every component
  
    // join the components into date
    return d.slice(0, 3).join('.') + ' ' + d.slice(3).join(':');
  }


  
  